.services-title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 125%;
  /* identical to box height, or 30px */
  text-align: center;
  letter-spacing: 0.05em;
  background: linear-gradient(
    232.68deg,
    #2dd1db -8.55%,
    #2d9cdb 57.36%,
    #1a8ed0 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.description-container {
  margin: 10px 80px;
}

.services-desc-p {
  margin: 50px 20px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 136%;
  text-align: center;
  letter-spacing: 0.05em;
  color: #565d5c;
}

.services-desc {
  font-size: 18px;
  line-height: 27px;
  text-align: center;
  color: #4a4a4a;
}

.services-card-header {
  margin: 0 0 5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.services-card-header-h3 {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 110%;
  /* identical to box height, or 20px */
  text-align: center;
  letter-spacing: 0.05em;
  color: #151515;
  margin: 20px 0 40px;
}

.service-cards {
  display: flex;
  flex-direction: column;
}

.services-cards-container {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.service-card-description {
  margin: 3rem 0;
  position: relative;
  background: #fff;
  border-radius: 10px;
  padding: 2rem;
  /* border: 1px solid #2D9CDB; */
}
.service-card-no-description {
  display: none;
  height: 0;
  width: 0;
}
.service-card-description .arrow-up:nth-child(1) {
  position: absolute;
  left: 9rem;
  transform: translate(-50%, 0);
  top: -2.9rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5rem;
  color: #ffffff;
  /* border: 1px solid #2D9CDB; */
}

