.service-card {
  cursor: pointer;
  position: relative;
  box-shadow: 0px 0px 38.1333px 13.1083px rgba(32, 115, 163, 0.3);
  max-width: 225px;
  max-height: 225px;
  border-radius: 19.0667px;
  border-radius: 15px;
  padding: 2rem;
  display: block;
  justify-content: center;
  text-align: center;
  align-items: center;
  transition: all 0.5s ease;
  background: linear-gradient(
    232.68deg,
    #2dd1db -8.55%,
    #2d9cdb 57.36%,
    #1a8ed0 100%
  );
}
.service-card:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 38.1333px 13.1083px rgba(32, 115, 163, 0.3);
  max-width: 230px;
  max-height: 245px;
  border-radius: 20px;
  border-radius: 15px;
  padding: 2rem;
  display: block;
  justify-content: center;
  text-align: center;
  align-items: center;
  background: linear-gradient(
    232.68deg,
    #2dd1db -8.55%,
    #2d9cdb 57.36%,
    #1a8ed0 100%
  );
}

.service-card-img {
  margin: 0 0 20px;
}
.service-card-title h6 {
  color: #fff;
  font-size: 18px;
}

.knowMore {
  font-family: "Poppins";
